// Print

@import '_fonts';
@import 'https://fonts.googleapis.com/css?family=Cormorant+Garamond:400,700';
@import 'https://fonts.googleapis.com/css?family=Lato:300,400';
@import 'https://fonts.googleapis.com/css?family=Roboto:400,700';
@import '_functions';
@import '_mixins';
@import '_colors';
@import '_general';

body {
    background: $white;
    text-rendering: geometricPrecision; }

#estimate {
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    padding: 0 6rem;
    width: 100%;
    .title {
        font-family: 'Cormorant Garamond', serif; }
    .data {
        padding-top: 4rem;
        margin-bottom: 2rem;
        b {
            font-family: 'Cormorant Garamond', serif;
            @include font-size(16px);
            font-weight: 700; } }
    .description {
        margin-bottom: 2rem;
        padding-left: 2rem; }
    & > .content {
        padding-left: 2rem;
        margin-bottom: 3rem;
        & > .title {
            position: relative;
            display: inline-block;
            margin-bottom: 1rem;
            &:before {
                position: absolute;
                content: '';
                background: $gray;
                width: .8rem;
                height: 1px;
                bottom: -.4rem;
                left: 0; } }
        .service_list {
            .service {
                margin-bottom: 1rem;
                padding-top: 1rem;
                .title {
                    @include font-size(16px); }
                ul {
                    list-style: url(../img/bullet.svg);
                    padding-left: 3rem;
                    li {
                        margin-bottom: .5rem;
                        position: relative; } }
                h1 {
                    @include font-size(16px);
                    font-family: 'Cormorant Garamond', serif;
                    font-weight: 700;
                    margin-top: 1rem;
                    margin-bottom: 1rem;
                    position: relative;
                    padding-left: .5rem; } } } }
    .calendar {
        padding-top: 3rem;
        padding-left: 2rem;
        margin-bottom: 3rem;
        page-break-before: always;
        .title {
            position: relative;
            display: inline-block;
            margin-bottom: 3rem;
            &:before {
                position: absolute;
                content: '';
                background: $gray;
                width: .8rem;
                height: 1px;
                bottom: -.4rem;
                left: 0; } }
        .subtitle {
            @include font-size(16px);
            font-family: 'Cormorant Garamond', serif;
            font-weight: 700;
            margin-bottom: 3rem;
            span {
                font-family: 'Lato', sans-serif;
                font-weight: 300; } }
        .table {
            .label {
                @include font-size(16px);
                font-family: 'Cormorant Garamond', serif;
                font-weight: 700; }
            .service {
                .label {
                    background: #EBEBEC;
                    padding: .2rem .4rem;
                    width: rem(170px); }
                .offset {
                    background: #F5F5F5; }
                .duration {
                    background: #666666; } }
            .days {
                background: $gray;
                text-align: center;
                font-weight: 400;
                padding: .3rem; }
            .days_full {
                width: rem(10px); } } }
    .investment {
        padding-left: 2rem;
        margin-bottom: 3rem;
        & > .title {
            position: relative;
            display: inline-block;
            margin-bottom: 2rem;
            &:before {
                position: absolute;
                content: '';
                background: $gray;
                width: .8rem;
                height: 1px;
                bottom: -.4rem;
                left: 0; } }
        .subtitle {
            @include font-size(16px);
            font-family: 'Cormorant Garamond', serif;
            font-weight: 700;
            margin-bottom: 1rem;
            padding-left: 1rem;
            & + .table {
                padding-left: 1rem; } }
        .subtitle_discount {
            @include font-size(16px);
            font-family: 'Cormorant Garamond', serif;
            font-weight: 700;
            margin-bottom: 1rem; }
        .table {
            margin-bottom: 1rem;
            .title {
                @include font-size(17px);
                background: #DDDCDC;
                font-weight: 700;
                padding: .2rem .4rem;
                width: rem(100px); }
            .price {
                @include font-size(17px);
                background: #F1F2F2;
                font-family: 'Roboto', sans-serif;
                font-weight: 700;
                padding: .2rem .4rem;
                width: rem(200px);
                .symbol {
                    color: #58595B;
                    &.detail {
                        color: #999999; } }
                .qty {
                    color: #58595B;
                    &.detail {
                        color: #999999; } }
                .details {
                    @include font-size(13px);
                    color: #A7A9AC;
                    font-weight: 400; } } } }
    .notes {
        page-break-before: always;
        padding-left: 2rem;
        padding-top: 3rem;
        margin-bottom: 3rem;
        .title {
            position: relative;
            display: inline-block;
            margin-bottom: 2rem;
            &:before {
                position: absolute;
                content: '';
                background: $gray;
                width: .8rem;
                height: 1px;
                bottom: -.4rem;
                left: 0; } }
        .list {
            list-style: url(../img/bullet.svg);
            padding-left: 1rem;
            .item {
                margin-bottom: .5rem; } } }
    .bank_details {
        padding-left: 2rem;
        margin-bottom: 3rem;
        .title {
            position: relative;
            display: inline-block;
            margin-bottom: 2rem;
            &:before {
                position: absolute;
                content: '';
                background: $gray;
                width: .8rem;
                height: 1px;
                bottom: -.4rem;
                left: 0; } } }
    .address {
        padding-left: 2rem;
        margin-bottom: 3rem;
        .title {
            position: relative;
            display: inline-block;
            margin-bottom: 2rem;
            &:before {
                position: absolute;
                content: '';
                background: $gray;
                width: .8rem;
                height: 1px;
                bottom: -.4rem;
                left: 0; } } }
    .footer {
        padding-bottom: 2rem;
        .signatures {
            @include clear;
            width: 100%;
            .person {
                float: left;
                width: 50%;
                text-align: center;
                position: relative;
                @include font-size(16px);
                font-family: 'Cormorant Garamond', serif;
                font-weight: 700;
                &.empty {
                    padding-top: rem(150px); }
                .signature {
                    width: 100%;
                    text-align: center;
                    .img {
                        width: rem(200px); } }
                .name {
                    display: inline-block;
                    position: relative;
                    &:before {
                        position: absolute;
                        content: '';
                        height: 1px;
                        width: rem(200px);
                        background: $black;
                        top: -.5rem;
                        left: 50%;
                        transform: translateX(-50%); } } } } } }
